import React, { useState } from 'react'
import Togglable from './Togglable'

const Blog = ({ blog, updateLikes, handleDelete }) => {
  const [likes, setLikes] = useState(blog.likes)

  const style = {
    margin: '5px 0',
    padding: '5px 10px',
    border: '1px solid black',
  }

  const addLikes = (event) => {
    event.preventDefault()
    setLikes(likes+1)
    updateLikes({
      ...blog
    })
  }
  const deletePost = (event) => {
    event.preventDefault()
    handleDelete({
      ...blog
    })
  }
  const isUsername = (blog) => {
    return blog.user !== undefined
      ? isOwner(blog.user.username)
      : false
  }
  const isOwner = (username) => {
    const currentUser = JSON.parse(window.localStorage.loggedInBlogUser).username
    return currentUser === username
    ? true
    : false
  }

  return (
    <div key={blog.id} style={style}>
      {blog.title + '　'}
      <Togglable buttonLabel='view' secondButtonLabel='hide'>
        <div>{blog.url+'\n'} </div>
        <div>{likes+'\n'} <input type="submit" onClick={addLikes} value='LIKE'/> </div>
        <div>{blog.author} </div>
        {isUsername(blog) &&
         window.confirm('Remove?') &&
          <input type='submit' onClick={deletePost} value='DELETE'/>
        }
      </Togglable>
    </div>
  )
}

export default Blog
